.logo {
    border: 5px solid white;
     height: 90px;
     width: 190px;
     display: flex;
     justify-content: center;
     align-items: center;
     font-size: 3.5rem;
     color: white;
     line-height: 0;
 }
 .info-section {
     padding: 54px;
     width: 35%;
     position: absolute;
     top: 0;
     bottom: 0;
     left: 0;
     .info-box {
         bottom: 60px;
         position: absolute;
         h4 {
             font-size: 1.2rem;
             color: $color3;
             margin: 0.7rem 0;
             p {
                 font-size: .9rem;
                 font-weight: 300;
                 margin: 0.2rem 0 1.6rem;
             }
         }
     }
     .icon-section {
         a {
             color: white;
             text-decoration: none;
             font-size: 1rem;
             padding: 0 5px;
             &:hover {
                 color: black;
             }
             &:first-child {
                 padding: 0 10px 0 0;
             }
         }
     }
 }